@import url(https://fonts.googleapis.com/css?family=Rye&display=swap);
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 80%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #003;
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 62.5%;
  }
}

.App {
  text-align: center;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.Cell {
  cursor: pointer;
  height: 5rem;
  width: 5rem;
  background-color: #263238;
}

.Cell:hover {
  background-color: #222930;
}

.Cell-lit {
  background-color: #029eb3;
}

.Cell-lit:hover {
  background-color: #00bcd4;
}

.shuffle {
  cursor: pointer;
  margin-top: 3rem;
  width: 100%;
  height: 3.5rem;
  font-size: 1.6rem;
  font-weight: bolder;
  text-transform: uppercase;
  color: #003;
  background-color: #e8ab0a;
  border: 1px solid #e87f00;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  outline: none;
  -webkit-transition: color, background 0.3s;
  transition: color, background 0.3s;
}

.shuffle:hover {
  background: #ff8c00;
  color: #003;
}

.shuffle:active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.box {
  text-align: center;
}

.neon,
.neon-small {
  font-family: neon;
  color: #fb4264;
  text-shadow: 0 0 3vw #f40a35;
  font-size: 4rem;
}

.neon {
  font-size: 9vw;
  line-height: 9vw;
}

.flux,
.flux-small {
  font-family: neon;
  color: #426dfb;
  text-shadow: 0 0 3vw #2356ff;
  font-size: 4rem;
  margin-left: 1.5rem;
}

.flux {
  font-size: 9vw;
  line-height: 9vw;
  display: block;
  margin-top: 2rem;
}

.neon {
  animation: neon 1s ease infinite;
  -moz-animation: neon 1s ease infinite;
  -webkit-animation: neon 1s ease infinite;
}

@-webkit-keyframes neon {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@keyframes neon {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

.flux {
  animation: flux 2s linear infinite;
  -moz-animation: flux 2s linear infinite;
  -webkit-animation: flux 2s linear infinite;
  -o-animation: flux 2s linear infinite;
}

@-webkit-keyframes flux {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

@keyframes flux {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

